@import '../../styles/constants';

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module_CaptureView {
    position: relative;
    height: 100%;
    width: 100%;
    background: #ffffff;

    & > * {
      position: absolute;
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
    }

    video {
      background: $backgroundColor;
    }

    video,
    & > canvas {
      object-fit: cover;
    }

    .ABBYY_Capture_Module__Messages {
      display: flex;
      justify-content: center;
    }

    .ABBYY_Capture_Module__Message_Popup {
      .ABBYY_Capture_Module__Message_Popup_Container {
        z-index: 1;
      }

      button {
        width: 208px;
        height: 52px;
        border-radius: 8px;
        background-color: #4a8ee0;
        color: #ffffff;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
      }
    }
  }
}
