@import '../../styles/constants';

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module_ImagePreview {
    display: flex;
    flex-direction: column;
    height: 100%;
    width: 100%;
    max-width: $maxDocumentWidth;

    .ABBYY_Capture_Module_ImageParentContainer {
      flex-grow: 1;
      display: flex;
      justify-content: center;
      align-items: center;

      img {
        max-width: 100%;
        max-height: inherit;
        object-fit: contain;
      }

      .ABBYY_Capture_Module-Show_Warnings_Button {
        position: absolute;
        width: 40px;
        height: 40px;
        left: calc(50% - 20px);
        border-radius: 8px;
        background-color: rgba(0, 0, 0, 0.8);
        bottom: 145px;

        &:after {
          position: absolute;
          margin: 8px;
          content: '';
          width: 24px;
          height: 24px;
          object-fit: contain;
          background-image: url(./warning.svg);
        }
      }
    }
    .ABBYY_Capture_Module__MessagePage {
      position: absolute;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
    }
  }
}
