$button_main_size: 40px;
$button_padding: 48px;

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module_CaptureView {
    .ABBYY_Capture_Module_CaptureView_ButtonContainer {
      display: flex;
      justify-content: space-between;
      align-items: flex-end;
      box-sizing: border-box;
      padding: $button_padding;

      input {
        display: none;
      }

      .ABBYY_Capture_Module_CaptureView_Button {
        position: relative;
        width: $button_main_size;
        height: $button_main_size;
        background-color: rgba(0, 0, 0, 0.4);
        border-radius: 8px;
        outline: none;
        cursor: pointer;

        &:after {
          content: '';
          top: 8px;
          left: 8px;
          width: 24px;
          height: 24px;
          position: absolute;
          object-fit: contain;
          display: block;
          color: #ffffff;
        }

        &.ABBYY_Capture_Module_CaptureView_Button__Get_File_Button {
          &:after {
            background-image: url(./ic-gallery.svg);
          }
        }

        &.ABBYY_Capture_Module_CaptureView_Button__Close_Button {
          &:after {
            background-image: url(./ic-close.svg);
          }

          z-index: 1000;
        }

        &:active {
          opacity: 0.6;
        }
      }

      .ABBYY_Capture_Module_CaptureView__Capture_Button {
        position: relative;
        display: block;
        border-radius: 50%;
        border: none;
        width: $button_main_size;
        height: $button_main_size;
        background-color: rgba(255, 255, 255, 0.82);
        outline: none;
        pointer-events: initial;
        cursor: pointer;

        &.ABBYY_Capture_Module_CaptureView__Capture_Button--disabled {
          background-color: rgba(255, 255, 255, 0.82);
          pointer-events: none;
          opacity: 0.3;
        }

        &:active {
          opacity: 0.6;
        }

        &:before {
          position: absolute;
          left: -8px;
          top: -8px;
          content: '';
          display: inline-block;
          width: 48px;
          height: 48px;
          background: transparent;
          border-radius: 56px;
          border: 4px solid rgba(255, 255, 255, 0.82);
        }
      }
    }
  }
}
