.ABBYY_Capture_Module {
  .ABBYY_Capture_Module_CaptureView {
    .online_capture-document {
      display: flex;
      justify-content: center;
      align-items: center;

      .online_capture-document_title {
        padding: 8px 16px;
        border-radius: 14pt;
        background-color: rgba(33, 33, 33, 0.72);
        display: flex;
        text-align: center;
        align-items: center;
        justify-content: center;
        line-height: 22pt;
        font-size: 17px;
        letter-spacing: 0.41pt;
        color: white;
      }

      img {
        position: absolute;
        width: 56px;
        height: 56px;
        object-fit: contain;
        display: block;

        &.online_capture-rectangle.rec-0 {
          top: 16px;
          left: 16px;
        }
        &.online_capture-rectangle.rec-90 {
          top: 16px;
          right: 16px;
          transform: rotate(90deg);
        }
        &.online_capture-rectangle.rec-180 {
          bottom: 16px;
          right: 16px;
          transform: rotate(180deg);
        }
        &.online_capture-rectangle.rec-270 {
          bottom: 16px;
          left: 16px;
          transform: rotate(270deg);
        }
      }
    }
  }
}
