@import '../../styles/constants';

$svg-extraSpace: 25px;

:export {
  svgExtraSpace: $svg-extraSpace;
}

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__CropView {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    flex-direction: column;
    position: relative;

    span.button {
      color: #ffdc48;
      cursor: pointer;
    }

    span {
      font-weight: normal;
      padding: 10px 0;
      text-align: center;
    }

    .ABBYY_Capture_Module__TopPanel {
      width: 100%;
      padding: 0 $padding;

      span {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        min-width: 20%;

        &:last-child {
          text-align: end;
        }
        &:first-child {
          text-align: start;
        }
      }

      span:active:not(.button) {
        opacity: 1;
      }
    }

    .ABBYY_Capture_Module__CropView_WorkingPane {
      flex-grow: 1;
      overflow: hidden;
      display: inline-flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      max-width: calc(#{$maxDocumentWidth} - 2 * #{$svg-extraSpace});
      position: relative;
      padding: $svg-extraSpace;

      & > div {
        position: relative;
        visibility: hidden; // will change to visible after resize

        img {
          display: block;
          max-width: 100%;
          max-height: inherit;
        }

        .ABBYY_Capture_Module__CropView_CropLayer {
          margin: -$svg-extraSpace;
          position: absolute;
          left: 0;
          top: 0;
          width: calc(100% + 2 * #{$svg-extraSpace});
          height: calc(100% + 2 * #{$svg-extraSpace});
        }
      }
    }

    .ABBYY_Capture_Module__CropView_BottomPane {
      width: 100%;
      padding: 0 $padding;
      color: #ffffff;

      .ABBYY_Capture_Module__CropView_AutocropContainer {
        text-align: center;
      }

      .ABBYY_Capture_Module__Navbar {
        width: 100%;
        height: $paneHeight;
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-top: 15px;
        line-height: $paneHeight;

        .ABBYY_Capture_Module__Navbar_prev,
        .ABBYY_Capture_Module__Navbar_next {
          @media only screen and (max-width: $TabletWidthThreshold - 1), screen and (min-width: $PCWidthThreshold) {
            background: transparent;
            position: relative;
            width: 24px;
            height: 24px;
            top: 0;

            &:after {
              top: 0;
              left: 0;
            }
          }
        }
      }
    }

    .ABBYY_Capture_Module__MessagePage {
      position: fixed;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
    }
  }
}
