@import '../ImagePreview/sizes';
@import '../../styles/constants';

.ABBYY_Capture_Module__Navbar {
  height: $paneHeight;
  display: flex;
  justify-content: space-around;
  margin-top: 6px;

  .ABBYY_Capture_Module__Navbar_pager {
    height: $paneHeight;
    align-self: center;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    line-height: 21px;
    text-align: center;
  }

  .ABBYY_Capture_Module__Navbar_prev,
  .ABBYY_Capture_Module__Navbar_next {
    width: $paneHeight;
    height: $paneHeight;
    position: absolute;
    top: calc(50% - #{$paneHeight / 2} - #{($bottom_bar_size + $paneHeight - $toolbar_size) / 2});
    background-color: rgba(0, 0, 0, 0.4);
    cursor: pointer;

    &:after {
      content: '';
      position: absolute;
      top: 12px;
      left: 16px;
      width: 24px;
      height: 24px;
      object-fit: contain;
      display: block;
      color: #ffffff;
    }

    &.ABBYY_Capture_Module__Navbar_next--disabled,
    &.ABBYY_Capture_Module__Navbar_prev--disabled {
      opacity: 0.3;
      cursor: default;
    }
  }

  .ABBYY_Capture_Module__Navbar_prev {
    left: 0;
    border-top-right-radius: 8px;
    border-bottom-right-radius: 8px;

    &:after {
      background-image: url(./ic-back.svg);
    }
  }
  .ABBYY_Capture_Module__Navbar_next {
    right: 0;
    border-top-left-radius: 8px;
    border-bottom-left-radius: 8px;

    &:after {
      background-image: url(./ic-next.svg);
    }
  }
}
