@import '../../styles/constants';

div.ABBYY_Capture_Module_Process_Page {
  background: $backgroundColor;
  align-items: center;
  z-index: 1000;

  span.ABBYY_Capture_Module__MessagePage-Text {
    width: 263px;
    height: 21px;
    text-align: center;
    color: #ffffff;
    padding-top: 24px;
  }

  .ABBYY_Capture_Module__MessagePage-Icon.ABBYY_Capture_Module_Process_Page_loader {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    object-fit: contain;
    background-image: url(./ic-gallery.svg);

    &:before {
      content: '';
      position: absolute;
      background-image: url(./loader.png);
      width: 56px;
      height: 56px;
      animation-name: spin;
      animation-duration: 1.2s;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
    }

    @keyframes spin {
      100% {
        transform: rotate(360deg);
      }
    }
  }
}
