@import '../../../styles/constants';
@import '../sizes';

.ABBYY_Capture_Module {
  .ABBYY_Capture_Module_BottomPanel {
    height: $bottom_bar_size;
    display: flex;
    flex-shrink: 0;
    justify-content: space-between;
    padding: 0 $padding;

    .ABBYY_Capture_Module_BottomPanel_button {
      height: $paneHeight;
      border-radius: 8px;
      border: none;
      background-color: rgba(255, 255, 255, 0.8);
      width: 100%;
      text-align: center;
      color: #424650;
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;

      &:first-child {
        margin-right: 16px;
      }

      &.ABBYY_Capture_Module_BottomPanel_button--save {
        background-color: #4a8ee0;
        color: #ffffff;
      }

      &:disabled {
        opacity: 0.3;
      }
    }
  }
}
