@import '../../styles/constants';

@font-face {
  font-family: 'SourceSansPro';
  src: url('../../assets/fonts/SourceSansPro-Regular.woff2') format('woff2');
}

@font-face {
  font-family: 'SourceSansPro';
  src: url('../../assets/fonts/SourceSansPro-Semibold.woff2') format('woff2');
  font-weight: 600;
}

body {
  overscroll-behavior: none;
}

.ABBYY_Capture_Module {
  background: $backgroundColor;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 17px;
  font-family: SourceSansPro, Arial, sans-serif;
  font-weight: 600;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: white;
  z-index: 9999;
  user-select: none;
  bottom: 0;
  right: 0;

  .ABBYY_Capture_Module__Container {
    max-width: $PCWidthThreshold;
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;
    display: flex;
    justify-content: center;

    @media only screen and (min-width: $PCWidthThreshold+1) {
      max-width: $maxDocumentWidth;
    }
  }

  /** global styles */
  button {
    cursor: pointer;
    font-size: 17px;
    font-family: SourceSansPro, Arial, sans-serif;
    font-weight: 600;

    &:active {
      opacity: 0.3;
    }

    &:disabled {
      cursor: default;
      opacity: 0.6;
    }
  }

  img {
    user-select: all;
  }
}
