.ABBYY_Capture_Module__MessagePage {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 100%;
  background: white;

  .ABBYY_Capture_Module__MessagePage-Icon {
    width: 24px;
    height: 24px;
