.ABBYY_Capture_Module {
  .ABBYY_Capture_Module__TopPanel {
    .ABBYY_Capture_Module__ToolBar_button {
      display: flex;
      justify-content: flex-start;
      flex-direction: row;
      align-items: center;
      font-size: 14px;
      padding: 10px 0;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      max-width: 25%;
      margin-right: 8px;
      cursor: pointer;

      &:last-child {
        margin-right: 0;
      }

      .ABBYY_Capture_Module__ToolBar_button_img {
        align-self: center;
        min-width: 24px;
        width: 24px;
        height: 24px;
        object-fit: contain;
        margin-right: 5px;

        &.ABBYY_Capture_Module__ToolBar_button_img--delete {
          background-image: url(./ic-del.svg);
        }

        &.ABBYY_Capture_Module__ToolBar_button_img--crop {
          background-image: url(./ic-crop.svg);
        }

        &.ABBYY_Capture_Module__ToolBar_button_img--retake {
          background-image: url(./ic-reshoot.svg);
        }

        &.ABBYY_Capture_Module__ToolBar_button_img--rotate {
          background-image: url(./ic-rotate.svg);
        }
      }
      &:active {
        opacity: 0.6;
      }
      &.ABBYY_Capture_Module__ToolBar_button--disabled {
        opacity: 0.3;
      }
    }
  }
}
