.ABBYY_Capture_Module_Wasm_Loading_Page {
  background: #ffffff;
  align-items: center;
  width: 100%;
  height: 100%;
  z-index: 1001;

  span.ABBYY_Capture_Module__MessagePage-Text {
    padding-top: 24px;
  }

  .ABBYY_Capture_Module_Wasm_Loading_Page_loader {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 24px;
    height: 24px;
    object-fit: contain;
    background-image: url(./ic-scan.svg);

    &:before {
      content: '';
      position: absolute;
      background-image: url(./loader.png);
      width: 56px;
      height: 56px;
      animation-name: spin;
      animation-duration: 1.2s;
      animation-iteration-count: infinite;
      animation-timing-function: linear;
    }

    @keyframes spin {
      100% {
        transform: rotate(360deg);
      }
    }
  }
}
