.ABBYY_Capture_Module__Message_Popup {
  display: flex;
  flex-direction: column;
  justify-content: center;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;

  .ABBYY_Capture_Module__Message_Popup_Container {
    min-width: 240px;
    padding: 16px;
    background: rgba(0, 0, 0, 0.8);
    border-radius: 8px;
    align-self: center;
    align-content: center;
    display: flex;
    flex-direction: column;

    .ABBYY_Capture_Module__Message-Icon {
      margin: 8px;
      align-self: center;
      width: 24px;
      height: 24px;
      object-fit: contain;

      &.ABBYY_Capture_Module__Message-Icon--warning {
        background-image: url(./warning.svg);
      }

      &.ABBYY_Capture_Module__Message-Icon--autocapture {
        background-image: url(./autocapture.svg);
      }
    }

    button {
      border: none;
      height: 20px;
      color: #ffdc48;
      background: transparent;
      align-self: center;

      &:active {
        opacity: 0.6;
      }
    }

    .ABBYY_Capture_Module__Message {
      display: flex;
      flex-direction: column;
      margin-bottom: 16px;

      .ABBYY_Capture_Module__Message-Title {
        margin: 0 auto 4px;
        text-align: center;
      }

      .ABBYY_Capture_Module__Message-Text {
        opacity: 0.8;
        font-size: 14px;
        text-align: center;
      }
    }
  }
}
